.tariff {
	min-width: 320px;
	min-height: 469px;

	position: relative;

	background-color: $grey;
}

.tariff::before {
	content: '';
	position: absolute;
	top: 0;
  width: 100%;
	height: 100%;
	background-image: url(../img/svg/bg-triangle-white.svg);
	background-repeat: no-repeat;
	background-position-x: center;
	background-position-y: top;
	background-size: 100% 31px;
	z-index: 0;
}

.tariff__wrapper {
display: flex;
flex-direction: column;
align-items: center;

width: 320px;

padding: 0 20px;
margin: 0 auto;
}

.tariff__table {
	border-collapse: collapse;

	margin-top: 91px;
}

.tariff__table--tablet {
	display: none;
}

.table__cell {
	display: none;
	width: 276px;
	height: 59px;
	background-color: $white;

	border: 2px solid #e5e5e5;
	border-top: none;

	&--top {
		height: 94px;
		background-color: $main-color;
		border-bottom: none;

	}

	&--visible {
		display: block;
	}
}

.table__content {
	&--top-name {
		@include font-style (18px, 16px, 700);
		color: $white;
		text-transform: uppercase;
		text-align: center;
	}

	&--top-price {
		@include font-style (24px, 0, 300);
		color: $white;
		text-align: center;
	}

	&--feature {
		@include font-style (14px, 30px);
		text-transform: uppercase;
		padding-left: 22px;
	}

	&--avail {
		background-image: url("../img/svg/icon-yes.svg");
		background-repeat: no-repeat;
		background-position-x: 228px;
		background-size: 31px 21px;
	}

	&--no-avail {
		background-image: url("../img/svg/icon-no.svg");
		background-repeat: no-repeat;
		background-position-x: 228px;
		background-size: 27px 26px;
	}
}

.tariff__toggles {
	margin-top: 41px;
}

@media (min-width: $width-tablet) {

	.tariff {
		min-width: 660px;
		min-height: 376px;
	}

	.tariff::before {
		background-size: 100% 49px;
	}

	.tariff__wrapper {
		width: 660px;
	}

	.tariff__table {
		margin-top: 88px;
		margin-bottom: 68px;
	}

	.tariff__table--mobile {
		display: none;
	}

	.tariff__table--tablet {
		display: table;
	}

	.table__cell {
		display: table-cell;
		width: 152px;
		height: 45px;

		&--top {
			height: 75px;
		}

		&--avail {
			background-image: url("../img/svg/icon-yes.svg");
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: 31px 21px;
		}
	
		&--no-avail {
			background-image: url("../img/svg/icon-no.svg");
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: 27px 26px;
		}
	}

	.table__content {
		&--top-name {
			@include font-style (16px, 16px, 700);
		}
	
		&--top-price {
			@include font-style (18px, 0, 300);
		}
	
		&--feature {
			margin: 0;
			padding-left: 15px;
			text-transform: none;
		}
	}

	.tariff__toggles {
		display: none;
	}

}

@media (min-width: $width-desktop) {

	.tariff {
		min-width: 1200px;
		min-height: 525px;
	}

	.tariff::before {
		background-size: 100% 80px;
	}

	.tariff__wrapper {
		width: 1200px;
	}

	.tariff__table {
		margin-top: 148px;
		margin-bottom: 0;
	}

	.table__cell {
		display: table-cell;
		width: 233px;
		height: 59px;

		&--top {
			height: 94px;
		}

	}

	.table__content {
		&--top-name {
			@include font-style (18px, 16px, 700);
		}
	
		&--top-price {
			@include font-style (24px, 0, 300);
		}
	
		&--feature {
			@include font-style (18px, 0);
		}
	}

}